.noStyleBtn {
    background: none;
    border: none;
    padding: 0;
    margin: 0;
    cursor: pointer;
    font-size: inherit;
    color: inherit;
    text-decoration: none;
    outline: none;
}
.borderColor {
    border-color: #d9d9d9 !important;
}

.dBlock {
    display: block !important;
}

.dNone {
    display       : none !important;
    pointer-events: none !important;
}
.show{
    display: flex;
    justify-content: center;
    align-items: center;
}
.show>.modal-dialog {
    animation: showModal 0.2s ease;
    display: flex;
    justify-content: center;
    align-items: center;
}

@keyframes showModal {
    0% {
        opacity: 0;
        top    : -10%;
    }

    100% {
        top    : 0%;
        opacity: 1;
    }

}

.show>.bgModalEvent { 
    animation: bgModalEventShow 0.2s ease;
}
 

@keyframes bgModalEventShow {
    0% {
        background: #00000000;
    }

    100% {
        background: #0000005c;
    }
}

.bgModalEvent {
    position  : absolute;
    width     : 100%;
    height    : 100%;
    background: #0000005c;
}

.customize-checkboxes>div {
    margin: 5px;
}
 

.fixedBot {
    display        : flex;
    flex-direction : column;
    justify-content: center;
    align-items    : center;
    width          : 100%; 
    position       : fixed;
    bottom         : 0px;
    z-index        : 10;
}

.pagidark li>a {
    background-color: #212529;
    color           : #fff;
}

.styletotalPgage {
    border-radius: 0.375rem;
}

.animFolder[data-set="SVGanim"]{ 
    opacity          : 1;
    -webkit-animation: flicker-in-1 1s linear both;
    animation        : flicker-in-1 1s linear both;
}
.animText{
    fill: black;
}
.animText[data-set="SVGanim"]{
    stroke-width     : 0;
    stroke-dashoffset: 0;
    fill             : #000000;
    /* 854 */
    animation        : animText 2s linear 1s both;
}
.shadowAnim[data-set="SVGanim"]{
    opacity: 0;
    animation        : shadowAnim 0.1s ease 2s;
}
@keyframes shadowAnim {
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}


@keyframes animText {
    0% {
        opacity          : 1;
        stroke-width     : 2;
        fill             : #fff;
        stroke-dashoffset: 854;
    }

    50% {
        stroke-width     : 2;
        fill             : #fff;
        stroke-dashoffset: 0;
        opacity          : 1;
    }

    51% {
        stroke-width: 0;
        fill        : #000000;
    }

    55% {
        opacity: 1;
    }

    55.1% {
        opacity: 0;
    }

    57% {
        opacity: 0;
    }

    57.1% {
        opacity: 1;
    }

    60% {
        opacity: 1;
    }

    60.1% {
        opacity: 0;
    }

    65% {
        opacity: 0;
    }

    65.1% {
        opacity: 1;
    }

    75% {
        opacity: 1;
    }

    75.1% {
        opacity: 0;
    }

    77% {
        opacity: 0;
    }

    77.1% {
        opacity: 1;
    }

    85% {
        opacity: 1;
    }

    85.1% {
        opacity: 0;
    }

    86% {
        opacity: 0;
    }

    86.1% {
        opacity: 1;
    }

    100% {
        opacity          : 1;
        stroke-dashoffset: 0;
        fill             : #000000;
    }
}


