
.svgLight{
  path, polyline,line, circle{
    stroke: #fff;
  }
  circle{
    fill:#fff;
  }
}
.svgDark{
  path, polyline,line, circle{
    stroke: #212529;
  }
  circle{
    fill:#212529;
  }
}

.modal.show {
  background: #000000bf;
} 
.darkFill {
  g{
    path, polyline,line, circle{
      fill: #212529;
    }
  }
  path, polyline,line, circle{
    fill: #212529;
  }
  circle{
    fill:#212529;
  }
}
.lightFill{
  g{
    path, polyline,line, circle{
      fill: #fff;
    }
  }
  path, polyline,line, circle{
    fill: #fff;
  }
  circle{
    fill:#fff;
  }
}
.deleteSvg{
  width: 21px;
  height: 21px;
  path, polyline,line,circle{ 
    transition: ease 0.1s ; 
  }
  &:hover{
    path, polyline,line,circle{
      transition: ease 0.1s ;
      fill: #ff7676;
    } 
  }
}
a{
  &:hover{
    svg{
      path, polyline,line,circle{
        transition: ease 0.1s ;
        fill: #006eff;
      } 
    }
  }
 
}
.ag-theme-alpine , .ag-theme-balham , .ag-theme-material , .ag-theme-quartz{
   svg{
    path, polyline,line, circle{
      stroke: #212529;
    }
    circle{
      fill:#212529;
    }
  }
  .deleteSvg{
    stroke-width: 4px;
  }
 
}
.ShowAnim{ 
  animation: ShowAnimKF 1s ease;
  @keyframes ShowAnimKF {
    0%{
      opacity: 0;
    }
    100%{
      opacity: 1;
    }
  }
}


// Collapsible Closed
details{ 
  background-color: #f1f1f1;
  border-radius: 1rem;
  box-shadow: 0 .25rem 1rem rgba(0, 0, 0, 0.2);
  
  &:not([open]){
    animation-name: fold-in;
    animation-duration: .2s;
  }
  
  summary{
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: 600;
    padding: 1rem;
  }
  
  // Disable browser default marker
  summary::-webkit-details-marker,
  summary::marker{
    content: "";
    display: none;
  }
  
  // Custom marker
  summary::after{
    content: "+";
    font-size: 1.5rem;
    font-weight: 400;
    line-height: 1;
    margin-right: 1rem;
    cursor: pointer;
  }
  
  .content{
    padding: 0 1.5rem 1.5rem;
  }
}

// Collapsible OPEN
details[open]{
  
  // Custom marker
  summary::after{
    content: "";
  }
  
  .content{
    animation-name: fold-out;
    animation-duration: .2s;
  }
}

// Fake it 'til you make it animations
@keyframes fold-out {
  0% {
    opacity: 0;
    margin-top: -1rem;
  }
  
  100% {
    opacity: 1;
    margin-top: 0;
  }
}

@keyframes fold-in {
  0% {
    padding-bottom: 1rem;
  }
  
  100% {
    padding-bottom: 0;
  }
}

.CloseContextMenu{
  position: fixed;
  width: 100vw;
  height: 100vh;
  background: #885e5e00;
  margin: 0;
  padding: 0;
  top: 0;
  left: 0;
}

 
